import { Duplex, Writable, Readable } from 'stream'
import Bus from 'bus'

function read(length) {

}

function write() {}

function setup() {
  setInterval(() => {
    this.write('0100\r1')
    setTimeout(() => {
      this.write('00os\r\n0sj')
    }, 500)
  }, 1000)
}

//const _serial = Serial.setup({ tx: B7, rx: B6 })

const bus = new Bus({ setup })

bus.frameSet.push({
  decode(chunk) {
    for(let index = 0; index < chunk.length; index ++) {
      if((chunk[index] === '\r'.charCodeAt()) && (chunk[index + 1] === '\n'.charCodeAt())) {
        const frame = this._busState.parsing.slice(0, index + 2)
        this._busState.parsing = Buffer.from(this._busState.parsing.slice(frame.length + 2, this._busState.parsing.length))
        return frame
      }
    }

    throw new Error('Not a frame')
  }
})

bus.setup()

bus.on('frame', frame => console.log('frame', frame))

